import axios from 'axios';
import authHeader from './auth-header';

const API_URL = 'http://192.168.0.158:8080/api/test/';

class UserService {
  getPublicContent() {
    return axios.get(API_URL + 'all');
  }

  getUserBoard() {
    return axios.get(API_URL + 'user', { headers: authHeader() });
  }

  getKeeperBoard() {
    return axios.get(API_URL + 'keeper', { headers: authHeader() });
  }

  getAdminBoard() {
    return axios.get(API_URL + 'admin', { headers: authHeader() });
  }

  getAllUsers() {
    return axios.get('http://192.168.0.158:8080/api/users', { headers: authHeader() });
  }

  setKeeperRole(id) {
    return axios.put(`http://192.168.0.158:8080/api/users/setkeeper/${id}`, null, { headers: authHeader() });
  }

  deleteUser(id) {
    return axios.delete(`http://192.168.0.158:8080/api/users/${id}`, { headers: authHeader() });
  }
}

export default new UserService();